<template>
<div class="login-page-wrapper">
    <MainPageHeader/>
    <section class="section">
        <div class="columns is-centered">
            <div class="column is-7">
                <div class="login-card is-bordered">
                    <div class="columns">
                        <div class="logo-wrapper column is-hidden-mobile">
                            <img src="@/assets/images/logo.png">
                        </div>
                        <div class="column">
                            <div class="personal-data-wrapper">
                                <div class="container">
                                    <div class="field">
                                        <InputField
                                                placeholder="Login"
                                                type="username"
                                                v-model="login"
                                        />
                                        <InputField
                                                placeholder="Password"
                                                type="password"
                                                v-model="password"
                                        />
                                    </div>
                                </div>
                                <div class="container">
                                    <div class="field">
                                        <Button
                                                text="Login"
                                                border-color="primary"
                                                @click="doLogin"
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</div>
</template>

<script>
import MainPageHeader from '@/components/MainPageHeader.vue';
import InputField from '@/components/input/InputField.vue';
import Button from '@/components/input/Button.vue';
import { getAuthService } from '../services/authService';
import { getHomePageForRole } from '../helpers/permissionHelper';

export default {
    name: 'LoginPage',
    components: {
        Button,
        InputField,
        MainPageHeader,
    },
    data() {
        return {
            login: '',
            password: '',
        };
    },
    methods: {
        async doLogin() {
            const loggedIn = await getAuthService().logIn(this.login, this.password);

            if (loggedIn) {
                const role = await getAuthService().getRole();
                this.$router.push(getHomePageForRole(role));
            }
        },
    },
};
</script>

